// Severity label component
const CveSeverityLabel: React.FC<{ severity: ImageCveRow['severity'] }> = ({ severity }) => {
  switch (severity) {
    case 'critical':
      return <Label color="red" icon={<TimesIcon />}>Critical</Label>;
    case 'important':
      return <Label color="orange" icon={<ExclamationTriangleIcon />}>Important</Label>;
    case 'moderate':
      return <Label color="yellow" icon={<ExclamationTriangleIcon />}>Moderate</Label>;
    case 'low':
      return <Label color="blue">Low</Label>;
  }
};

// Status label component
const CveStatusLabel: React.FC<{ status: ImageCveRow['status'] }> = ({ status }) => {
  switch (status) {
    case 'fixed':
      return <Label color="green" icon={<CheckCircleIcon />}>Fixed</Label>;
    case 'affected':
      return <Label color="red">Affected</Label>;
    case 'not affected':
      return <Label color="grey">Not affected</Label>;
  }
};

